.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
ul.setlist li {
  border: 2px solid;
  padding: 19px;
  margin-right: 14px;
}ul.setlist {
  display: inline-block;
  width: 100%;
  background: transparent;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
  border-right:1px solid #bbb;
}

li:last-child {
  border-right: none;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover:not(.active) {
  background-color: #111;
}form input[type="text"] {
  width: 100%;
  padding: 13px;
  margin-bottom: 14px;
  font-size: 25px;
}form {
  width: 506px;
  margin: 42px auto;
  text-align: center;
  border: 1px solid #a99d9d;
  overflow: hidden;
  padding: 0 73px;
}button {
  width: 100%;
  padding: 13px;
  margin: 0;
  margin-bottom: 14px;
  font-size: 25px;
  background: green;
  color: #fff;
}.pokemon_list {
  width: 81%;
  margin: 0 auto;
  text-align: center;
  border: 1px solid #0000004a;
  padding: 20px;
  border-radius: 4px;
}h2.card_image {
  width: 171px;
  height: 175px;
}h2.card_image img {
  width: 100%;
  height: 100%;
}

.active {
  background-color: #4CAF50;
}div#root {
  max-width: 1401px;
  margin: 0 auto;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
